<template>
	<view>
		<u-cell-group v-for="(item) in devicesList">
			<u-cell icon="wifi" :title="item.name" :value="item.RSSI"></u-cell>
		</u-cell-group>
	</view>
</template>

<script>
	let xBlufi = require("@/static/utils/blufi/xBlufi.js")
	export default {
		data() {
			return {
				devicesList: [],
				searching: false,
			}
		},
		onLoad() {
			xBlufi.initXBlufi(1);
			xBlufi.listenDeviceMsgEvent(true, this.funListenDeviceMsgEvent);
			this.search()
		},
		methods: {
			search(){
				if (this.searching) {
						xBlufi.notifyStartDiscoverBle({
				        'isStart': false
				    })
				} else {
						xBlufi.notifyStartDiscoverBle({
				        'isStart': true
				    })
				}
			},
			funListenDeviceMsgEvent: function(options) {
				let that = this
			    switch (options.type) {
			
			     
			      case xBlufi.XBLUFI_TYPE.TYPE_GET_DEVICE_LISTS:
			        if (options.result){
						that.devicesList.push(options.data)
					}
			      case xBlufi.XBLUFI_TYPE.TYPE_CONNECTED:
			        console.log("连接回调：" + JSON.stringify(options))
			        if (options.result) {
			          uni.hideLoading()
			          uni.showToast({
			            title: '连接成功',
			            icon: 'none'
			          })
			          // uni..navigateTo({
			          //   url: '../device/device?deviceId=' + options.data.deviceId + '&name=' + options.data.name,
			          // });
			
			        } else {
			          // wx.hideLoading()
			          // wx.showModal({
			          //   title: '提示',
			          //   content: '连接失败',
			          //   showCancel: false
			          // });
			        }
						break;
			
			      case xBlufi.XBLUFI_TYPE.TYPE_GET_DEVICE_LISTS_START:
			        if (!options.result) {
			          console.log("蓝牙未开启 fail =》", options)
			          uni.showToast({
			            title: '蓝牙未开启',
			            icon: 'none'
			          })
			        } else {
			          //蓝牙搜索开始
			         that.searching = true
			        }
			        break;
			
			      case xBlufi.XBLUFI_TYPE.TYPE_GET_DEVICE_LISTS_STOP:
			        if (options.result) {
			          //蓝牙停止搜索ok
			          console.log('蓝牙停止搜索ok')
			        } else {
			          //蓝牙停止搜索失败
			          console.log('蓝牙停止搜索失败')
			        }
					that.searching = false
			        break;
			
			    }
			  },
		}
	}
</script>

